<template>
  <div id="settings">
    <select id="size" v-model="size" @change="updateBoard">
      <option disabled value="">Please select one a grid size</option>
      <option v-for="opt in options" :key="opt">{{opt}}</option>
    </select>
    <label for="size">Grid character size</label><br/>
    <input type="checkbox" id="show-words" v-model="showWords" />
    <label for="show-words">Display words in grid</label>
  </div>
</template>

<script>
import { mapActions } from 'vuex'
import lodash from 'lodash'

export default {
  name: 'Settings',
  data () {
    return {
      options: [7,9,12,14,16,20]
    }
  },
  computed: {
    size: {
      get () {
        return this.$store.state.size
      },
      set (value) {
        this.$store.commit('updateSize', value)
      }
    },
    showWords: {
      get () {
        return this.$store.state.showWords
      },
      set (value) {
        this.$store.commit('updateShowWords', value)
      }
    }
  },
  methods: {
    updateBoard: _.debounce(function() {
      this.$store.dispatch('updateBoard')
      }, 700),
  }
}
</script>

<style scoped>
#settings{
    background: beige;
    width: 500px;
    margin: 0 auto;
    padding: 20px 0;
    border-radius: 12px;
}
label {
    padding: 10px;
}
</style>
